.button {
  border-radius: 100px;
  font-size: 1rem;
  height: 35px;
  position: absolute;
  right: 20px;
  margin: 1px auto;
  bottom: 0px;
  transition: .5s cubic-bezier(.68, -.55, .265, 1.55);
  width: 35px;
}

.button:focus {
  width: 63px;
  background-color: rgb(37, 35, 35) !important;
  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.2) !important;
  color: #91ccc2 !important;
  transition: 0.3s;
}

.buttonActive {
