.display-flex {
  display: flex;
  margin-top: 50px;
}

.display {
  height: 90px;
  background-color: white !important;
  display: flex;
  align-items: center;
  cursor: pointer;
  position: relative;
  background: var(--white);
  box-shadow: 0 2px 8px #5353531f;
  border-radius: 8px;
  border: none;
  padding-left: 15px;
  padding-right: 15px;
  margin-right: 25px;
  width: 33.3333%;
}

.socialmedia-div.row {
  margin-top: 60px;
  flex: 0 0 !important;
  margin-left: 100px;
}

.icon {
  margin-right: 20px;
  top: 30%;
}

.homepage_font {
  font-family: 'Roboto', sans-serif;
}

.all-channels.container p.homepage_font {
  font-size: 13px;
}

.padd {
  position: relative;
  top: 10px;
}

.displayy {
  height: 90px;
  background-color: white !important;
  display: flex;
  align-items: center;
  cursor: pointer;
  position: relative;
  background: var(--white);
  box-shadow: 0 2px 8px #5353531f;
  border-radius: 8px;
  border: none;
  padding-left: 15px;
  padding-right: 15px;
  margin-right: 25px;
  width: 31.1111%;
  /* margin-bottom: 50px; */
}

.iconns {
  height: 50px;
}

.gmail {
  height: 37px;
}

.qq {
  height: 51px;
  margin-left: 6px;
}

.iconnss {
  margin-right: 18px;
}

.wechat {
  height: 36px;
}

.tiktok {
  height: 53px;
}

@media screen and (max-width: 600px) {
  .display-flex {
    display: block !important;
    margin-top: 20px;
    margin-left: -80px;
  }

  .display {
    margin-right: 20px;
    margin-left: 82px;
    background-color: white;
    width: 80%;
    margin-bottom: 15px;
  }

  .displayy {
    height: 90px;
    background-color: white !important;
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;
    background: var(--white);
    box-shadow: 0 2px 8px #5353531f;
    border-radius: 8px;
    border: none;
    padding-left: 15px;
    padding-right: 15px;
    margin-right: 25px;
    width: 80.111%;
    margin-bottom: 50px;
    margin-left: 82px;
  }

  h6.channel_content_h6 {
    font-size: 14px;
  }

  button.socialmedia-btn {
    margin-top: 10px;
  }

  a.list-group-item.fontss {
    margin-left: 53px;
  }

  .mobile-toggle-icon,
  .search-toggle-icon,
  .projects,
  .notifications,
  .messages {
    position: relative;
    left: 7px;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-color: #fff;
    color: #5E636B;
    font-size: 16px;
    cursor: pointer;
    border-radius: 50%;
    border: 1px solid rgb(255 255 255 / 0%);
    box-shadow: 0 0.125rem 0.25rem rgb(0 0 0 / 8%);
  }

  .padd {
    position: relative;
    top: 10px;
    left: 12px;
    padding-right: 10px;
  }

  .icon {
    margin-right: 0px !important;
    top: 30%;
  }

  .gmail {
    height: 38px !important;
  }

  .qq {
    margin-right: 2px;
  }

  .YellowPages {
    /* margin-right: 18px; */
  }

  .iconnss {
    margin-right: 0px !important;
  }

  .wechat {
    height: 40px;
  }

  .tiktok {
    height: 57px;
  }
}


